<template>
  <v-content>
    <v-dialog :value="show" persistent width="600">
      <successAlert
        :show="createTrainerNotification"
        :text="alertText"
        @closed=" createTrainerNotification = false;"
      ></successAlert>
      <v-dialog v-model="loading" persistent width="300">
        <v-card color="primary" dark>
          <v-card-text class="center">
            {{$t("addAndEditTrainer.loadingTextAdd")}}
            <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-sheet>
        <v-form ref="form" v-model="valid" autocomplete="off">
          <v-row justify="center" style="margin:0">
            <h1>{{$t("addAndEditTrainer.titleAdd")}}</h1>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.firstName")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="trainer.firstName"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.lastName")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="trainer.lastName"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.email")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="trainer.email"
                :full-width="true"
                filled
                :rules="[rules.required,rules.email]"
                :error-messages="wrongEmail"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.password")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="trainer.password"
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required]"
                :type="show1 ? 'text' : 'password'"
                name="input-10-1"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
                hint="At least 8 characters"
                @click:append="show1 = !show1"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.age")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                type="number"
                v-model="trainer.age"
                min="0"
                :rules="[rules.required]"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.sex")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="trainer.sex"
                :items="values.sex"
                item-value="id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              >
                <template v-slot:item="{item}">{{ item.name[$i18n.locale] }}</template>
                <template v-slot:selection="{ item }">{{item.name[$i18n.locale]}}</template>
              </v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.level")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="trainer.level"
                :items="values.level"
                item-value="id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              >
                <template v-slot:item="{item}">{{ item.name[$i18n.locale] }}</template>
                <template v-slot:selection="{ item }">{{item.name[$i18n.locale]}}</template>
              </v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.active")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-checkbox
                v-model="trainer.active"
                style="padding:0 0 25px 40%;height:10px;margin:0"
              ></v-checkbox>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.canVerifyPayments")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-checkbox
                v-model="trainer.verifyPayments"
                style="padding:0 0 25px 40%;height:10px;margin:0"
              ></v-checkbox>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.payment")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                type="number"
                v-model="trainer.paymentNumber"
                :full-width="true"
                filled
                :rules="[rules.required]"
                min="0"
                dense
                rounded
                class="fieldStyle"
                :suffix="values.currency[user.currency].symbol"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.clients")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-row justify="center">0</v-row>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0;" class="mx-auto" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditTrainer.notes")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-textarea v-model="trainer.notes" filled rounded dense></v-textarea>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="padding:20px 3% 20px 3%" align="center" justify="space-between" no-gutters>
            <v-btn @click="close" color="error" :normal="true">{{$t("addAndEditTrainer.close")}}</v-btn>
            <v-btn
              color="primary"
              @click="createClient"
              :normal="true"
            >{{$t("addAndEditTrainer.create")}}</v-btn>
          </v-row>
        </v-form>
      </v-sheet>
    </v-dialog>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import successAlert from "./successAlert";

export default {
  name: "AddTrainer",
  components: { successAlert },
  props: ["show"],
  data() {
    return {
      sex: ["Male", "Female", "Other"],
      level: ["Amateur", "Professional"],
      program: ["Week", "Month", "Year"],
      target: ["Gain mass", "Lose weight"],
      menu2: false,
      valid: false,
      loading: false,
      show1: false,
      alertText: "Trainer created",
      createTrainerNotification: false,
      trainer: {
        firstName: "",
        lastName: "",
        age: "",
        sex: "",
        level: "",
        active: false,
        verifyPayments: false,
        payment: "",
        paymentNumber: "",
        client: "",
        password: "",
        email: "",
        systemType: ""
      },
      rules: {
        required: value => !!value || "Required.",
        min: v => v.length >= 8 || "Min 8 characters",
        email: v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      }
    };
  },

  computed: {
    ...mapGetters(["types", "values", "user", "wrongEmailCreate"]),
    wrongEmail() {
      const errors = [];
      if (this.wrongEmailCreate) {
        this.stopLoading();
        errors.push("This email is in use");

        return errors;
      } else {
        return [];
      }
    }
  },
  watch: {
    show: function() {
      this.$store.commit("wrongEmailCreate", false);
    }
  },
  methods: {
    stopLoading: function() {
      this.loading = false;
    },
    close: function() {
      this.trainer = {};
      this.$refs.form.resetValidation();
      this.$emit("closed", this.show);
    },
    createClient: async function() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        this.trainer.systemType = this.user.systemType;
        await this.$store.dispatch("createTrainer", this.trainer);
        await this.$store.dispatch("getTrainers");
        this.$store.commit("wrongEmailCreate", false);
        this.loading = false;
        this.createTrainerNotification = true;
        this.trainer = {};
        this.$refs.form.resetValidation();
      }
    }
  }
};
</script>

<style scoped>
.center {
  text-align: center;
}
.fieldStyle {
  height: 44px;
  margin-top: 5px;
}
</style>