<template>
  <v-snackbar v-model="show" color="success" multi-line :timeout="timeout" top right>
    <v-icon style="color:white;">check_circle</v-icon>

    <h3>{{text}}</h3>
    <v-btn dark text @click="closeAlert">Close</v-btn>

    <!-- <v-alert style="width:auto;height:auto" type="success"></v-alert> -->
  </v-snackbar>
</template>

<script>
export default {
  name: "successAlert",
  props: ["text", "show"],
  data() {
    return {
      timeout: 3000
    };
  },
  methods: {
    closeAlert() {
      this.$emit("closed");
    }
  }
};
</script>