<template>
  <v-content>
    <v-dialog eager :value="show" persistent width="600">
      <successAlert
        :show="createClientNotification"
        :text="alertText"
        @closed="createClientNotification = false"
      ></successAlert>
      <v-dialog v-model="loading" persistent width="300">
        <v-card color="primary" dark>
          <v-card-text class="center">
            {{$t("addAndEditClient.loadingTextAdd")}}
            <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
          </v-card-text>
        </v-card>
      </v-dialog>
      <v-sheet>
        <v-form ref="form" v-model="valid">
          <v-row justify="center" style="margin:0">
            <h1>{{$t("addAndEditClient.titleAdd")}}</h1>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.firstName")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="client.firstName"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.lastName")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="client.lastName"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.email")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="client.email"
                :full-width="true"
                filled
                :error-messages="wrongEmail"
                :rules="[rules.required,rules.email]"
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.password")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                v-model="client.password"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required]"
                :type="showPassword ? 'text' : 'password'"
                name="input-10-1"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
                hint="At least 8 characters"
                @click:append="showPassword = !showPassword"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.age")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                type="number"
                v-model="client.age"
                min="0"
                :rules="[rules.required]"
                :full-width="true"
                filled
                dense
                rounded
                class="fieldStyle"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.sex")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="client.sex"
                :items="values.sex"
                item-value="id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              >
                <template v-slot:item="{item}">{{ item.name[$i18n.locale] }}</template>
                <template v-slot:selection="{ item }">{{item.name[$i18n.locale]}}</template>
              </v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.level")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="client.level"
                :items="values.level"
                item-value="id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              >
                <template v-slot:item="{item}">{{ item.name[$i18n.locale] }}</template>
                <template v-slot:selection="{ item }">{{item.name[$i18n.locale]}}</template>
              </v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.trainer")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="client.trainer"
                :items="trainersArray"
                :item-text="fixName"
                item-value="_id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              ></v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.active")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-checkbox v-model="client.active" style="padding:0 0 25px 40%;height:10px;margin:0"></v-checkbox>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.payment")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-checkbox
                v-model="client.payment"
                style="padding:0 0 25px 40%;height:10px;margin:0"
              ></v-checkbox>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.paymentType")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="client.paymentType"
                :items="values.paymentType"
                item-value="id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              >
                <template v-slot:item="{item}">{{ item.name[$i18n.locale] }}</template>
                <template v-slot:selection="{ item }">{{item.name[$i18n.locale]}}</template>
              </v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.program")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="client.program"
                :items="values.program"
                item-value="id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              >
                <template v-slot:item="{item}">{{ item.name[$i18n.locale] }}</template>
                <template v-slot:selection="{ item }">{{item.name[$i18n.locale]}}</template>
              </v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.lastWeight")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                type="number"
                :full-width="true"
                v-model="client.lastWeightNumber"
                filled
                :rules="[rules.required]"
                min="0"
                dense
                rounded
                class="fieldStyle"
                :suffix="values.systems[user.systemType].weight.symbol"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.height")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-text-field
                type="number"
                v-model="client.heightNumber"
                :full-width="true"
                filled
                :rules="[rules.required]"
                min="0"
                dense
                rounded
                class="fieldStyle"
                :suffix="values.systems[user.systemType].height.symbol"
              ></v-text-field>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.target")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-select
                v-model="client.target"
                :items="values.target"
                item-value="id"
                :full-width="true"
                filled
                :rules="[rules.required]"
                dense
                rounded
                class="fieldStyle"
              >
                <template v-slot:item="{item}">{{ item.name[$i18n.locale] }}</template>
                <template v-slot:selection="{ item }">{{item.name[$i18n.locale]}}</template>
              </v-select>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0;" class="mx-auto" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.startWeekAt")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-menu
                v-model="menu2"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="client.startDate"
                    label="
                Select a date"
                    :rules="[rules.required]"
                    readonly
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  :rules="[rules.required]"
                  v-model="client.startDate"
                  :allowed-dates="allowedDates"
                  @input="menu2 = false"
                ></v-date-picker>
              </v-menu>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="margin:0;" class="mx-auto" align="center" justify="center">
            <v-col class="center">{{$t("addAndEditClient.notes")}}</v-col>
            <v-divider vertical></v-divider>
            <v-col>
              <v-textarea v-model="client.notes" filled rounded dense></v-textarea>
            </v-col>
          </v-row>
          <v-divider></v-divider>
          <v-row style="padding:20px 3% 20px 3%" align="center" justify="space-between" no-gutters>
            <v-btn @click="close" color="error" :normal="true">{{$t("addAndEditClient.close")}}</v-btn>
            <v-btn
              color="primary"
              @click="createClient"
              :normal="true"
            >{{$t("addAndEditClient.create")}}</v-btn>
          </v-row>
        </v-form>
      </v-sheet>
    </v-dialog>
  </v-content>
</template>

<script>
import { mapGetters } from "vuex";
import moment from "moment";
import successAlert from "./successAlert";

export default {
  name: "AddClient",
  components: { successAlert },
  props: ["show"],
  data() {
    return {
      menu2: false,
      valid: false,
      loading: false,
      showPassword: false,
      createClientNotification: false,
      timeout: 3000,
      alertText: "Client created",
      client: {
        firstName: "",
        lastName: "",
        age: "",
        sex: "",
        level: "",
        active: false,
        payment: false,
        program: "",
        lastWeightNumber: "",
        heightNumber: "",
        target: "",
        startDate: "",
        endDate: "",
        notes: "",
        password: "",
        email: "",
        systemType: ""
      },
      rules: {
        required: value => !!value || "Required.",
        min: v => v.length >= 8 || "Min 8 characters",
        email: v => /.+@.+\..+/.test(v) || "E-mail must be valid"
      }
    };
  },
  computed: {
    ...mapGetters(["types", "values", "user", "trainers", "wrongEmailCreate"]),
    trainersArray: function() {
      let array = this.trainers.filter(trainer => {
        return trainer.active === true;
      });
      return array;
    },
    wrongEmail() {
      const errors = [];
      if (this.wrongEmailCreate) {
        this.stopLoading();
        errors.push("This email is in use");

        return errors;
      } else {
        return [];
      }
    }
  },
  watch: {
    show: function() {
      this.$store.commit("wrongEmailCreate", false);
    }
  },
  methods: {
    stopLoading: function() {
      this.loading = false;
    },
    allowedDates: function(date) {
      const today = moment().format("YYYY-MM-DD");
      return moment(today).isSameOrBefore(date);
    },
    fixName: function(item) {
      return `${item.firstName} ${item.lastName}`;
    },
    close: function() {
      this.client = {};
      this.$refs.form.resetValidation();
      this.$emit("closed", this.show);
    },
    createClient: async function() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        this.client.systemType = this.user.systemType;
        this.client.startDate = moment(this.client.startDate).format(
          "YYYY-MM-DD"
        );
        if (this.client.program == 4000) {
          this.client.endDate = moment(this.client.startDate)
            .add(1, "weeks")
            .format("YYYY-MM-DD");
        } else if (this.client.program == 4001) {
          this.client.endDate = moment(this.client.startDate)
            .add(1, "months")
            .format("YYYY-MM-DD");
        } else if (this.client.program == 4002) {
          this.client.endDate = moment(this.client.startDate)
            .add(1, "years")
            .format("YYYY-MM-DD");
        }
        await this.$store.dispatch("createClient", this.client);
        await this.$store.dispatch("getClients");
        this.$store.commit("wrongEmailCreate", false);
        this.loading = false;
        this.createClientNotification = true;
        this.client = {};
        this.$refs.form.resetValidation();
      }
    }
  }
};
</script>

<style scoped>
.center {
  text-align: center;
}
.fieldStyle {
  height: 44px;
  margin-top: 5px;
}
</style>
